import Vue from 'vue'

export function SignatureInit(keysn='0002',password='123456', isCheck=false,num=1){
    function delCB(signatureid, signatureData) {
        for (var key in Signature.list) {
            if (signatureid == key && Signature.list[signatureid].keysn == signatureData.keysn) {
                var signatureCreator = Signature.create();
                signatureCreator.removeSignature(signatureData.documentid, signatureid);
                break;
            }
        }
        return true;
    }
    Signature.init({//
        //keysn:'0741170010110516',
        keysn, // 'test001', // 002
        password,
        //keysn:'test001',
        //usercode:"test002",
        delCallBack: delCB,
        //icon_remove : false,// false
        //icon_sign : false, // false
        // imgtag: 0, //0; 1:; 2:; 3:; 4:; 5:
        timestamp: true,
        certType : 'server',//
        sealType : 'server',//
        autoCert:true,  
        // serverUrl : 'http://192.168.4.222:8080/iSignatureHTML5/html5',
        // serverUrl : `${Vue.prototype.$ipSign}/iSignatureHTML5/html5`,
        serverUrl : `${Vue.prototype.$ipSign}/iSignatureHTML5`,
        //serverUrl : 'http://173.101.1.134:8089/iSignatureHTML5/html5',
        //showSealsDlg:'auto',  // 
        //serverUrl : 'http://192.168.0.151:8080/iSignatureHTML5',
        documentid:'KG2016093001333',//ID
        documentname:'KG2016093001',//
        //quickClose: false,//
        verifyResType : 'click touchend',//clickIOSwebview
        pw_timeout:'s1800', //sh:d:
        scaleImage: num?1.15:null||1, //
        showNoPW:true, // bu
        signdate:{
            ischeck:!isCheck,
            fontSize:12,
            position:'()',
        },
     });

}



